.header{z-index:20;background-color:hsl(var(--background)/0.8);position:fixed;width:100%;box-sizing:border-box;padding:15px;backdrop-filter:blur(12px)}.header .background{background-color:hsl(240,10%,3.9%);opacity:.5;height:100%;width:100%;position:absolute;left:0;top:100%}.header .bar{display:flex;justify-content:flex-end;text-transform:lowercase;font-size:12px;font-weight:400;position:relative}.header .bar p{margin:0px}.header .bar a{text-decoration:none;color:hsl(var(--foreground));position:absolute;left:0}.header .bar .shopContainer{display:flex;gap:30px;position:absolute;right:0}.header .bar .shopContainer .shop{display:none}.header .bar .el{display:flex;align-items:center;justify-content:center;gap:12px;cursor:pointer}.header .bar .el .label{position:relative;display:flex;align-items:center}.header .bar .el p:nth-of-type(2){position:absolute;opacity:0}.header .bar .el .burger{width:22.5px;position:relative;pointer-events:none}.header .bar .el .burger::after,.header .bar .el .burger::before{content:"";height:1px;width:100%;background-color:hsl(var(--foreground));position:relative;display:block;transition:all 1s cubic-bezier(0.76, 0, 0.24, 1)}.header .bar .el .burger::after{top:-4px}.header .bar .el .burger::before{top:4px}.header .bar .el .burgerActive::after{transform:rotate(45deg);top:-1px}.header .bar .el .burgerActive::before{transform:rotate(-45deg);top:1px}@media only screen and (min-width: 600px){.header{padding:20px}.header .bar{font-size:15px}.header .shop{display:block;cursor:pointer}}